.Assignments-pic .tab2 {
  min-height: 400px;
}
.Assignments-pic .tab2 .Empty-main {
  padding-top: 60px;
}
.Assignments-pic .tab2 .search-title {
  margin-bottom: 18px;
}
.Assignments-pic .tab2 .ant-card-bordered {
  background: #F9F9F9;
  border-radius: 5px;
  margin-top: 8px;
}
.Assignments-pic .tab2 .ant-card-bordered .ant-card-body {
  margin: 20px 30px 20px 30px;
}
.Assignments-pic .tab2 .ant-card-bordered .ant-row {
  position: relative;
}
.Assignments-pic .tab2 .ant-card-bordered .ant-row .ant-col-22 {
  width: 96%;
}
.Assignments-pic .tab2 .tab2-center-img {
  width: 4%;
  height: 55px;
  line-height: 55px;
  position: relative;
}
.Assignments-pic .tab2 p {
  margin-bottom: 0;
  height: 30px;
  line-height: 30px;
}
.Assignments-pic .tab2 .title1 .title-span {
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  width: 84%;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.Assignments-pic .tab2 .title1 span {
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
}
.Assignments-pic .tab2 .title1 .right-i {
  min-width: 128px;
  float: right;
  font-size: 12px;
  text-align: center;
  color: #2A283D;
  height: 28px;
  line-height: 28px;
}
.Assignments-pic .tab2 .title1 .right-i span {
  font-size: 12px;
  opacity: 1;
  font-weight: 600;
}
.Assignments-pic .tab2 .title1-active .right-i span {
  color: #2A283D;
  opacity: 0.3;
}
.Assignments-pic .tab2 .title1-on .right-i span {
  color: #FF9142;
  opacity: 0.99;
}
.Assignments-pic .tab2 .title2 span {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  margin-bottom: 0;
}
.Assignments-pic .tab2 .title2 .right-i {
  min-width: 128px;
  float: right;
  text-align: center;
}
.Assignments-pic .tab2 .title2 .right-i .ant-btn {
  background: #3BB9FF;
  border: 0.539754px solid #309DE4;
  box-sizing: border-box;
  border-radius: 3.77828px;
  height: 28px;
  line-height: 28px;
  color: #FFFFFF !important;
}
.Assignments-pic .tab2 .title2 .right-i .a-link,
.Assignments-pic .tab2 .title2 .right-i span {
  color: #FFFFFF !important;
  opacity: 1;
}
.Assignments-pic .tab2 .title3 span {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  margin-bottom: 0;
}
.Assignments-pic .tab2 .title3 .right-i {
  min-width: 128px;
  float: right;
  text-align: center;
  margin: 0 auto;
}
.Assignments-pic .tab2 .title3 .right-i .ant-btn {
  text-align: center;
  display: inline-block;
  background: #E8E8E8;
  border: 0.539754px solid #E2E2E2;
  box-sizing: border-box;
  border-radius: 3.77828px;
  height: 28px;
  line-height: 28px;
}
.Assignments-pic .tab2 .title3 .right-i span {
  color: #FFFFFF;
  opacity: 1;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
